<template>
    <div class="lessonInformation">
        <div class="header">
            <span @click="back" class="back">
                <img src="../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="more" @click="toggleMenu">
                 <img src="../assets/img/more.png">
            <ul v-show="isShow" class="moreMenu">
                <li v-for="(action, index) in actions" :key="index">{{ action.link }}</li>
            </ul>
            </span>
        </div>
        <div class="container">
            <div class="information">
                 <div class="inforLeft">
                    <span class="headPic">
                        <img :src="userInfo.fPhoto">
                    </span>
                </div>
                <div class="inforRight">
                    <span class="details">
                        <span class="userName">{{ userInfo.fName }}</span>
                        <span class="userBelong">
                            <span>所在位置：</span>{{ userInfo.fAddress }}
                        </span>
                        <span class="userDetail">个人简介：{{ userInfo.fIntroduce }}</span>
                    </span>
                </div>
            </div>
        </div>
        <div class="lessonDetails">
            <div class="lessons">
              <div class="lesson">
                <div class="lessonLeft">
                  <span>{{ lessonInfo.fLessonDate }}</span>
                </div>
                <div class="lessonRight">
                  <ul class="lessonInfor">
                      <li>课程：{{ lessonInfo.fLessonName }}</li>
                      <li>价格：￥{{ lessonInfo.fLessonPrice}} 元</li>
                      <li>时间：{{ lessonInfo.fFargStart }}-{{ lessonInfo.fFragEnd }}</li>
                      <li>地址：{{ lessonInfo.fLessonAddress }}</li>
                  </ul>
                </div>
              </div>
              <div class="accountNum">
                  <span class="number">报名人数：</span>
                  <span class="textOrange">{{ lessonInfo.fOrderCnt }}/{{ lessonInfo.fLimitCnt }}</span>
              </div>
            </div>
        </div>
        <div class="notice">
            <span>温馨提示：学员到达现场后,教练现场确认,确认到场即时扣款;若学员没有到场,将按原路返还预付金额,以保障学员权益。</span>
        </div>
        <div class="contact">
            <span class="consult" @click="contact">咨询</span>
            <span class="appointment" @click="$router.push('/home/teachers/appointment')">立即预约</span>
        </div>
        <div class="mask" v-show="hasMask" @click="toggleMenu"></div>
    </div>
</template>

<script>
import { getTeacherOfLesson, getLessonInfo } from '../api/getdata'
import store from '../vuex/store'
export default {
  name: 'lessonInfor',
  data () {
    return {
      fLessonId: '',
      isShow: false,
      hasMask: false,
      actions: [
        {link: '收藏'},
        {link: '分享'},
        {link: '举报'}
      ],
      userInfo: {},
      lessonInfo: {}
    }
  },
  mounted () {
    this.fLessonId = store.state.lessonId
    this.getUserInfo()
    this.getLessonDetails()
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    toggleMenu () {
      this.isShow = !this.isShow
      this.hasMask = !this.hasMask
    },
    getLessonDetails () {
      let data = getLessonInfo({lessonId: this.fLessonId})
      data.then(res => {
        // console.log(res.data)
        this.lessonInfo = res.data
      })
    },
    getUserInfo () {
      let data = getTeacherOfLesson({lessonId: this.fLessonId})
      data.then(res => {
        // console.log(res.data)
        this.userInfo = res.data
      })
    },
    contact () {
      this.$router.push('/home/community/chat')
    }
  }
}
</script>

<style scoped>
* {
    margin: 0;
    padding: 0;
    font-size: 0.48rem;
  }

  .userInfor {
    width: 100%;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .more {
    height: 0.6rem;
    width: 0.13rem;
    position: relative;
  }

  .more img,
  .back img {
    width: 100%;
  }

  .moreMenu {
    position: absolute;
    list-style-type: none;
    background: #f5f5f5;
    top: 1.2rem;
    right: 0.5rem;
    border-radius: 0.2rem;
    z-index: 99;
    padding: 0 0.65rem;
    box-shadow: -1px 1px 3px #f5f5f5;
  }

  .moreMenu li {
    width: 1.466667rem;
    color: #ff8309;
    padding: 0.5rem 0;
    font-size: 0.4266667rem;
  }

  .moreMenu li:nth-child(2) {
    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
  }

  .container {
    border-bottom: 0.10667rem solid #e6e6e6;
    padding-bottom: 0.1rem;
  }

  .information {
    display: flex;
    width: calc(100% - 0.8rem);
    margin: 0 auto;
  }

  .headPic {
    width: 1.6rem;
    height: 1.6rem;
    display: inline-block;
    border-radius: 50%;
    overflow: hidden;
  }

  .headPic img {
    width: 100%;
    height: 100%;
    border-radius: 0.8rem;
  }

  .inforRight {
    margin-left: 0.48rem;
    margin-top: 0.5rem;
  }

  .details span {
    display: block;
    text-align: start;
  }

  .userName {
    margin-bottom: 0.15rem;
  }

  .userBelong,
  .userDetail {
    font-size: 0.346667rem;
    line-height: 0.6rem;
    color: #666666;
  }

  .userBelong span:first-child {
    display: inline-block;
    color: #333333;
    font-size: 0.346667rem;
  }

  .lessonDetails {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    background: #f5f5f5;
    border-radius: 0.2rem;
  }

  .lesson {
    display: flex;
    margin: 0.38rem auto;
    padding: 0.3rem 0.2rem;
    border-bottom: 1px solid #e6e6e6;
    color: #333333;
  }

  .lessonLeft span {
    width: 1.65rem;
    display: inline-block;
    vertical-align: top;
    font-size: 0.373333rem;
  }

  .lessonInfor {
    list-style-type: none;
    text-align: start;
  }

  .lessonInfor li {
    font-size: 0.373333rem;
  }

  .lessonRight {
    position: relative;
    margin-left: 0.48rem;
  }

  .accountNum {
    width: 100%;
    text-align: start;
    padding: 0 0 0.3rem 0.2rem;
  }

  .number {
    font-size: 0.4rem;
    color: #333333;
  }

  .textOrange {
    font-size: 0.4rem;
    color: #ff8309;
  }

  .notice {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    text-align: start;
    padding-top: 0.2rem;
    line-height: 0.4rem;
  }

  .notice span {
    font-size: 0.32rem;
    color: #666666;
  }

  .contact {
    display: flex;
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 1.3rem;
    border-top: 1px solid #e6e6e6;
  }

  .consult,
  .appointment {
    display: inline-block;
    width: 50%;
    text-align: center;
    line-height: 1.3rem;
  }

  .appointment {
    background: #ffd200;
  }

  .mask {
    z-index: 9999;
    position: absolute;
    top: -10rem;
    width: 100%;
    height: 2000%;
    background: #ffffff;
    opacity: 0;
  }
</style>



